    opacity: 1; } }

.snakeModal {
  width: calc(100vw - 20px);
  height: calc(100vh - 20px);
  position: absolute;
  top: 0px;
  left: 0px;
  background: red; }

.snakeModal .boder {
  border-radius: 50%;
  border: solid 5px green;
  margin: 50px auto;
  height: calc(100vh - 150px);
  width: calc(100vh - 150px);
  background-color: gray; }

.snakeModal .boder.in {
  -webkit-animation: shake 0.5s;
          animation: shake 0.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; }

.snakeModal .boder.start .cover {
  left: calc(50vw - 500px); }

.snakeModal .boder.start.in .cover {
  left: auto; }

.snakeModal .boder .cover {
  border-radius: 50%;
  border: 0px;
  margin: 10px auto;
  height: calc(100vh - 180px);
  width: calc(100vh - 180px);
  background-color: gray;
  z-index: 100;
  position: fixed;
  transition: left 2s;
  left: 100vw; }

@-webkit-keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg); }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg); }
  20% {
    transform: translate(-3px, 0px) rotate(1deg); }
  30% {
    transform: translate(3px, 2px) rotate(0deg); }
  40% {
    transform: translate(1px, -1px) rotate(1deg); }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg); }
  60% {
    transform: translate(-3px, 1px) rotate(0deg); }
  70% {
    transform: translate(3px, 1px) rotate(-1deg); }
  80% {
    transform: translate(-1px, -1px) rotate(1deg); }
  90% {
    transform: translate(1px, 2px) rotate(0deg); }
  100% {
    transform: translate(1px, -2px) rotate(-1deg); } }

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg); }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg); }
  20% {
    transform: translate(-3px, 0px) rotate(1deg); }
  30% {
    transform: translate(3px, 2px) rotate(0deg); }
  40% {
    transform: translate(1px, -1px) rotate(1deg); }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg); }
  60% {
    transform: translate(-3px, 1px) rotate(0deg); }
  70% {
    transform: translate(3px, 1px) rotate(-1deg); }
  80% {
    transform: translate(-1px, -1px) rotate(1deg); }
  90% {
    transform: translate(1px, 2px) rotate(0deg); }
  100% {
    transform: translate(1px, -2px) rotate(-1deg); } }

.snakeModal .groupItem {
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -40px;
  /* Negative half of height. */
  margin-left: -175px;
  /* Negative half of width. */ }

.snakeModal .item {
  width: 100px;
  height: 100px;
  background-color: hotpink;
  margin: 10px;
  display: inline-block; }

.snakeModal .cover .btnOpen {
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -40px;
  /* Negative half of height. */
  margin-left: -25px;
  /* Negative half of width. */
  width: 100px;
  height: 50px;
  line-height: 50px;
  display: none; }

.snakeModal .boder.start.in .btnOpen {
  display: block; }

body {
  font-family: 'Poppins', sans-serif;
  background-color: #fcfcfc;
}

.chat-area, .home {
  margin-top: 60px;
  position: relative;
}

.chat-area {
  padding: 20px;
  height: 400px;
  overflow-y: scroll;
}

.chat-area .chat-bubble {
  background-color: #bbdefb;
  padding: 20px;
  border-radius: 4px;
  max-width: 300px;
  word-wrap: break-word;
}

.chat-bubble .chat-time {
  font-size: 12px;
  color: #757575;
  text-align: right;
}

.chat-area .chat-bubble.current-user {
  margin-left: auto;
  background-color: #a5d6a7;
}

.form-control {
  width: 60%;
}

.form-control:focus {
  box-shadow: none;
}

.btn {
  margin-top: 10px;
}

.btn-submit {
  background-color: #2e7d32;
  color: white;
}

@media screen and (max-width: 767px) {
  .form-control {
    width: 100%;
  }

  .chat-area .chat-bubble {
    max-width: 250px;
  }
}
